import React, { useState } from 'react';
import validUrl from 'valid-url'
import ApiManager from '../Api'
import './App.css';

function App() {
  const [longUrl, setLongUrl] = useState('')
  const [shortUrl, setShortUrl] = useState(null)
  const [isValid, setIsValid] = useState(true)

  const handleLongUrl = (e) => {
    setLongUrl(e.target.value)
  }

  const handleSubmit = async (e) => {
    e.preventDefault()

    if (validUrl.isUri(longUrl)) {
      setIsValid(true)
      let res = await new ApiManager().createLink(longUrl)
      console.log(res)
      setShortUrl(res.shortLink)
    } else setIsValid(false)
  }

  return (
    <div >
      <form onSubmit={handleSubmit}>
        <label>
          Long Url:
          <input
            type="text"
            name="long-url"
            value={longUrl}
            onChange={handleLongUrl}
          />
        </label>
        <input type="submit" value="Submit" />
      </form>

      {!isValid && <p>Please add a valid URL. e.g. https://www.google.com</p>}

      {shortUrl &&
        <>
          <p>Generated Short Link:</p>
          <a href={`${window.location.origin}/${shortUrl}`}>{`${window.location.origin}/${shortUrl}`} </a>

          <p>Orignal Link:</p>
          <a>{longUrl} </a>
        </>
      }
    </div>
  );
}

export default App;
