import axios from 'axios'

export default class ApiManager {
    async createLink(link) {
        let res
        try {
            res = await axios.post(`/generate`, { link }, { ...Headers })
            return res.data
        } catch (err) {
            return false
        }
    }
    async deleteLink(params) {
        let res
        try {
            res = await axios.delete(`/${params}`)
            return res.data
        } catch (err) {
            return false
        }
    }
}